.banner {
    position: relative;
    color: #fff;
    background: linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.45) 100%);

    .banner-content {
        position: absolute;
        left: 0;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }
    
    .title {
        font-size: 30px;
        margin-bottom: 0;
    }

    img.banner-img {
        display: block;
        position: relative;
        z-index: -1;
        width: 100%;
        height: 100%;
        min-height: 170px;
        object-fit: cover;
    }

    @include media-breakpoint-up(md) {
        
        .title {
            font-size: 60px;
        }
    }
}

