.ana-sayfa {
    .abs-bar3 {
        width: 100%;
        position: absolute;
        background: #f4f4f4;
        height: 88px;
        border: none;
        top: -44px;
        z-index: -1;
    }

    .products {
        position: relative;
        padding: 132px 0 185px;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            background-color: $primary;
            width: 70%;
            min-height: 470px;
            @include media-breakpoint-up(md) {
                bottom: 0;
            }

            @each $breakpoint, $container-max-width in $container-max-widths {
                @include media-breakpoint-up($breakpoint) {
                    $solboluk: 50vw - #{$container-max-width} / 2;
                    width: calc(#{$solboluk} + (#{$container-max-width} / 3));
                }
            }
        }

        .img-container {
            position: relative;
            z-index: 1;
        }

        .products-details-sub {
            position: relative;

            .text-container {
                position: relative;
                display: flex;
                flex-direction: column;
                justify-content: space-evenly;

                .bottom-border {
                    position: relative;
                    margin-bottom: 20%;
                    margin-top: 20%;

                    &::before {
                        content: "";
                        border: 1px solid #f0f0f0;
                        height: 0;
                        display: block;
                        margin-left: -110px;
                        $solboluk: 100vw;
                        margin-right: calc((#{$solboluk}) / -2);

                        @each $breakpoint, $container-max-width in $container-max-widths {
                            @include media-breakpoint-up($breakpoint) {
                                $solboluk: 100vw - #{$container-max-width};

                                margin-right: calc((#{$solboluk}) / -2);
                            }
                        }
                    }
                }

                .title {
                    font-weight: 600;
                    font-size: 40px;
                    color: #6b6d85;
                }

                .description {
                    font-family: $font-family-sans-serif-2;
                    font-size: 20px;
                    line-height: 1.75;
                    color: #9595aa;
                }
            }
        }
    }

    .capacity {
        background: #2e2c65;
        padding-top: 160px;
        padding-bottom: 220px;
        margin-bottom: $font-size-sm * -1;

        .title {
            margin-top: 50px;
            width: 100%;
            height: 80px;
            font-weight: 600;
            font-size: 40px;
            color: #fff;
        }

        .description {
            font-family: $font-family-sans-serif-2;
            font-size: 20px;
            line-height: 35px;
            color: #9595aa;
            max-width: 729px;
        }

        .img {
            img {
                border-bottom-right-radius: 6.25rem;
            }
        }
    }

    .mavi {
        position: relative;
        z-index: 1;

        .img-bg {
            position: absolute;
            right: 0;
            max-height: 100%;
            object-fit: cover;

            @each $breakpoint, $container-max-width in $container-max-widths {
                @include media-breakpoint-up($breakpoint) {
                    $solboluk: ((50vw - #{$container-max-width}) / 2);

                    width: calc(108px + #{$container-max-width} + (#{$solboluk}));
                }
            }
        }

        .settings {
            position: relative;
            z-index: 1;
            padding: 80px 0 100px;

            .title {
                font-weight: 600;
                font-size: 50px;
                text-align: left;
                color: #fff;
            }

            .links {
                display: flex;
                flex-wrap: wrap;
                margin: 0 -12px;

                .item {
                    width: 20%;
                }

                a {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: calc(100% - 24px);
                    padding: 40px;
                    margin: 12px;
                    font-weight: 600;
                    font-size: 1.375rem;
                    line-height: 1.36;
                    text-align: center;
                    color: #fff;
                    border-radius: 6px;
                    background: transparent;
                    border: 1px solid #91d2f5;

                    &:hover {
                        background-color: #ffff;
                        color: #2e2c65;
                    }
                }
            }
        }

        @include media-breakpoint-up(md) {
            margin: -44px 0;

            .settings {
                padding: 200px 0 170px;
            }
        }
    }

    .iso-anasayfa {
        background-color: #f4f4f4;
    }
}

@include media-breakpoint-down(xl) {
    .ana-sayfa {
        .mavi {
            .mavi-img {
                .settings {
                    width: 100%;
                    left: 10%;
                    top: 10%;
                    .box {
                        // height: 100px;
                    }
                }
            }
        }
        .capacity {
            text-align: center;
            margin-bottom: 0;
            .title {
                margin-top: 25px;
                margin-bottom: 40px;
            }
            .description {
            }
        }
        button {
            position: relative;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}
@include media-breakpoint-down(lg) {
    .anaSayfa {
        .absBar {
            display: none !important;
        }
    }
    .products {
        .title {
            min-width: 300px;
        }
        .products-details-sub {
            .text-container {
                width: 100%;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                width: 100%;
                top: 0 !important;
                height: 500px !important;
                justify-content: space-around;
                align-items: center;
                .text:first-of-type {
                    padding-bottom: 30px;
                    border-bottom: 1px solid #f0f0f0;
                }
                .bottom-border {
                    display: none;
                }
            }
        }
    }
}

@include media-breakpoint-down(md) {
    .ana-sayfa {
        .mavi .settings .links a {
            padding: 0px;
            min-width: 90%;
            max-width: 240px;
            max-height: 110px;
            min-height: 110px;
        }
        .products {
            .products-list {
                max-height: 422px;
                min-height: 422px;
                height: 422px;

                .text-container {
                    top: 0 !important;
                    margin: 0 !important;
                }
            }
        }

        .products-details-sub {
            .text-container {
                margin-top: 100px;
            }
        }
    }
}
