.bar {

    nav {

        li {
            position: relative;
            padding: 18px 0;
        }

        a {
            font-weight: 500;
            font-size: 24px;
            color: #ababaa;

            &:hover {
                color: #6b6d85;

                &:before {
                    content: "";
                    width: 100px;
                    height: 100px;
                    background-image: url("../../temp/diagonal-arrow.png");
                    background-repeat: no-repeat;
                    position: absolute;
                    top: 40px;
                    left: -30px;
                }
            }
        }
    }
}
