#carousel-main {
  counter-reset: count 0;
  color: #fff;
  
  .carousel-control-next-icon,
  .carousel-control-prev-icon {
    display: none;
  }

  .img-container {

    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.45) 100%);
    }

    img {
      width: 100%;
    }
  }

  .carousel-caption {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: auto;
    right: auto;
    transform: translateY(-50%);
    bottom: auto;
    text-align: left;
  }

  .carousel-description {
    font-family: $font-family-sans-serif-2;
    font-size: 20px;
    line-height: 1.75;
  }

  .carousel-title {
    font-weight: 600;
    font-size: 80px;
    color: #fff;
  }

  .text-carousel {
    position: relative;
    counter-increment: count;
    color: #fff;
    display: flex !important;

    [data-bs-target] {
      text-align: left;
      text-indent: 0;
      font-size: 24px;
      color: #fff;
      width: 100%;
      height: auto;
      opacity: 1;
      background-color: transparent;
      border-bottom: 2px solid #D8D8D8;
      padding-bottom: 28px;
      margin-right: 2rem;

      &.active {
        border-bottom-color: #ff5951;
      }
    }

    &:before {
      content: counter(count);
      font-weight: 600;
      font-size: 30px;
      margin-right: 30px;
      margin-top: 12px;
    }

    &:nth-child(-n + 9)::before {
      content: "0" counter(count);
    }
  }

  .carousel-inner {

    .carousel-item {
      
      img {
        min-height: 800px;
        width: 100%;
      }
    }
  }

  .carousel-indicators {
    margin: 0;
    display: block;
    bottom: 58px;
  }

  .slick-slide {
    padding: 0 !important;
  }

  .slick-list {
    padding: 0;
    text-align: left;
  }

  .swiper {
    width: 100%;
    padding: 0;
    text-align: left;
  }
}

@include media-breakpoint-up(md) {

  .caro {

    .carousel-indicators {
      position: absolute;
      bottom: 0;
      z-index: 2;
      padding: 0;
      margin-right: initial;
      margin-bottom: 1rem;
      margin-left: initial;
      list-style: none;
      padding: 0 !important;
    }
  }
}

@include media-breakpoint-down(lg) {
  
  #carousel-main {
    
    .slick-slide {
      margin: 0 0px;
    }

    .text-carousel {

      div {
        min-width: 140px;
        font-size: 18px;

        span {
          display: block;
        }
      }

      button {
        display: inline-block;
        position: relative;
        left: 40px ;
        max-width: 100%;
      }

    }

    .slick-list {
      margin: 0 -26px;
    }

    .text-carousel::before {
      font-size: 20px;
      left: 20px;
    }

    .carousel-description {
      font-size: 16px;
      text-align: center;
      line-height: 1.2 !important;
      min-width: 300px;
      position: relative;
      left: 50%;
      transform: translateX(-50%) !important;
    }
    
    .carousel-title {
      font-size: 40px;
      width: 100%;
      min-width: 300px;
      position: relative !important;
    }

    .carousel-inner {
      .carousel-item {
        img {
          min-height: 400px !important;
        }
      }
    }

    .carousel-indicators {
      position: absolute;
    }

  }
}

@include media-breakpoint-down(md) {

  #carousel-main {

    .carousel-caption {
      top: 125px;
      left: 50%;
      transform: translate(-50%, 0);
    }

    .carousel-title {
      white-space: nowrap;
    }

    .slick-slide {
      margin: 0 0px;
    }

    .slick-list {
      margin: 0 0px;
    }

    .text-carousel {
      
      [data-bs-target] {
        font-size: 18px;
      }

      .carousel-title {
        min-width: 140px;
      }
      
      button {
        font-size: 12px;
        min-width: 120px;
        left: 20px;
      }

      &::before {
        margin-left: 10px;
        margin-right: 0;
      }
    }
  }
}
