.iso {
    padding: 80px 0;
    text-align: center;

    .iso-content {
        position: relative;

        img {
            width: 100%;
        }
        
        .item {
            border-right: 1px solid #e7e7e7;

            &:last-child {
                border-right: 0;
            }
        }
    }

    @include media-breakpoint-up(md) {
        padding: 180px 0;
    }
}
