.footer {
    border-top: 1px solid #DCDCDC;

    .col {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        padding: 40px 45px;
        font-family: $font-family-sans-serif-2;
        font-size: 20px;
        line-height: 1.75;
        color: #9595aa;

        .title {
            font-family: $font-family-sans-serif;
            font-weight: 600;
            font-size: 30px;
            color: #6b6d85;
            margin-bottom: 35px;
        }

        a {
            font-family: $font-family-sans-serif;
            position: relative;
            color: #9595aa;
            font-size: 24px;
            text-decoration: none;

            &:hover {
                color: #2e2c65;

                &:before {
                    content: "";
                    width: 100px;
                    height: 100px;
                    background-image: url("../../temp/diagonal-arrow.png");
                    background-repeat: no-repeat;
                    position: absolute;
                    top: 20px;
                    left: -30px;
                }
            }
        }

        @include media-breakpoint-up(md) {
            min-height: 420px;
        }
    }

    .border-end {
        border-bottom: 1px solid #DCDCDC;

        @include media-breakpoint-up(md) {
            border-bottom: 0;
            border-right: 1px solid #DCDCDC;
            // border-bottom: 1px solid #DCDCDC;
        }
    }

    .logo {
        border-top: 1px solid #DCDCDC;
        padding: 20px;
    }
}